import axios from 'axios'

export default{
  //state   
  //nutations actions getter 
  namespaced: true, //nutations actions getter 
  strict: true,
  state:{
    products: [],
    categories: [],
  },
  //
  actions: {
    getProducts(context) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      //{ root: true } 
      context.commit('LOADING',true , { root: true })
      console.log("context",context)
      axios.get(url).then((response) => {
        console.log(response)
        //response.data.products  PRODUCTS
        context.commit('PRODUCTS',response.data.products)
        //response.data.products  CATEGORIES
        context.commit('CATEGORIES',response.data.products)
        console.log(':', response);
        context.commit('LOADING',false, { root: true });
      });
    },
  },
  //
  //()statestate
  //(ajaxsettimeout)statepayload
  mutations: {
    PRODUCTS(state,payload){
      state.products = payload;
      //
      console.log("payload",payload)
    },
    CATEGORIES(state,payload){
      // categories 
      const categories = new Set();
      console.log("categories",categories)
      //state
      console.log("CATEGORIESstate",state)
      //
      console.log("CATEGORIESpayload",payload)
      payload.forEach((item) => {
        //item.categorycategories
        categories.add(item.category);
      });
      console.log("forEachcategories",categories)
      //state.categories
      state.categories = Array.from(categories);
      console.log("state.categories",state.categories)
    },
  },
  getters: {
    categories: state =>state.categories,
    products: state => state.products
  }
}